export default {
	data() {
		return {
			carouselList: [], // 轮播列表
			goodsList: [],  // 商品列表
		}
	},
	onLoad() {
		this.loadData();
	},
	methods: {
		async loadData() {
			this.carouselList = await this.$api.json('carouselList');
			let goodsList = await this.$api.json('goodsList');
			this.goodsList = goodsList || [];
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/goods-detail/goods-detail?id=${id}`
			})
		},
	}
}
